
import Search from 'vux/src/components/search/index.vue'

export default {
components: {Search},
props:{
results: Array,
},
data(){
return{
value: ''
}
},
methods:{
resultClick (item) {
this.$emit('resultClick', item)
},
getResult (val) {
this.$emit('searchList',val)
},
onFocus () {
console.log('onFocus')
},
onCancel () {
console.log('on cancel')
},
onBlur(){
this.$refs.search.setBlur()
},
setValue(data){
this.value = data;
this.onBlur()
}
},
mounted(){
this.$nextTick(() => {
var searchFixed = document.getElementsByClassName('vux-search-box')[0];
var headerHeight  = document.getElementsByClassName('search_box')[0].offsetHeight
searchFixed.style.top = headerHeight + this.$StatusbarHeight + 1+ 'px'
})

}

}



