export default {
    name:'EquipmentReportDetail',
    props:{
        isShowfooter: Boolean
    },
    data(){
        return{
            titleName:'',
            judgeFlage:'',
            isShow: this.isShowfooter ? true: false
        }
    },
    mounted(){
        switch(this.$route.query.flag){
            case '1':
            this.titleName = '申请报告';
            break;
            case '2':
            this.titleName = '申请计量';
            break;
            case '3':
            this.titleName = '申请物料';
            break;
            case '4':
            this.titleName = '申请设备';
            break;
            case '5':
            this.titleName = '申请值班';
            break;
            case '6':
            this.titleName = '申请试验方法';
            break;
            case '7':
            this.titleName = '申请工装';
            break;
        }
    },
    methods:{
        setButton(flag){
            this.$emit('setButton',flag)
        }
    }
}



