export default {
    props:{
      titleName: String,
      operatingNum: String
    },
    data(){
        return{
            operating: 1 ,
        }
    },
    computed:{
        changeRotate(operating){
            if(this.operating == 0){
                return 'rotate(-140deg)'
            }else if(this.operating == 1 ){
                return 'rotate(38deg)'
            }else{
                return 'rotate('+ (1-this.operating-0.1 ) * -120 + 'deg)'
            }
        },
    },
    methods:{
      toPoint(){
          if(this.operatingNum){
             this.operating = this.operatingNum.replace("%", "")/100;
          }else{
             this.operating = 0
          }
        },
        goDetailBat(){
          this.$emit('goDetail')
        },
    },
    mounted(){
      this.toPoint();
    }
};



