
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {Scroller, LoadMore, Divider},
props:{
islistData:Boolean,
isBounce:Boolean
},
data(){
return{
pageNumber:1,
scrollerStatus: {
pullupStatus: 'default'
},
isLading: false,
isloadMore: false,
Bounce: this.isBounce ? false: true,//由于数据量过大，这里做反向处理，isBounce为true时不显示边缘回弹效果
}
},
methods:{
goDeatil(){
this.$emit("goDeatil");
},

onScrollBottom(){
this.$nextTick(()=>{
this.isLading = true;
this.pageNumber += 1;
var _this = this;
this.$emit("loadmore",this.pageNumber, res =>{
this.scrollpullupStatus(false,false);
},err =>{
this.scrollpullupStatus(true,false);
})
})
},
scrollpullupStatus(flag){
this.isLading = false;
this.isloadMore = flag;
this.scrollerStatus.pullupStatus =flag? 'disabled':'default';
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
})
},
},
mounted(){
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0});

})

},
watch:{
islistData(){
if(this.islistData){
this.scrollpullupStatus(false);
}
},
isloadMore(){
if(this.isloadMore){
this.islistData = false;
}
}
}
}



